import { css, modifier, styled, variants } from '@lokalise/styled';

import type { Alignment, Status } from './TopBanner';
import { spacing, typography, value } from '../../utils/theme';
import type { PressableProps } from '../Pressable';
import { Pressable } from '../Pressable';

type ContainerProps = {
	$styleStatus: Status;
	$alignItems: Alignment;
};

export const Container = styled.div<ContainerProps>`
	font-family: ${value('font.family.default')};
	padding: ${spacing(4)};
	display: grid;
	grid-template-columns: 1fr auto;
	column-gap: ${spacing(3)};
	align-items: flex-start;
	${modifier(
		({ $alignItems }: ContainerProps) => $alignItems === 'center',
		css`
			align-items: center;
		`,
	)};
	${variants('$styleStatus', {
		error: css`
			background-color: ${value('color.background.danger')};
		`,
		warning: css`
			background-color: ${value('color.background.warning')};
		`,
		info: css`
			background-color: ${value('color.background.info')};
		`,
	})};
`;

export const Content = styled.div`
	${typography('body.small.default')};
	color: ${value('color.text.default')};
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	column-gap: ${spacing(1)};
`;

export const TopBannerLink = styled(Pressable)<PressableProps>`
	${typography('body.small.default')}
	color: ${value('color.text.default')};
	text-decoration: underline;
	min-width: fit-content;
	border: none;
	background: none;

	&:hover,
	&:visited,
	&:link,
	&:active,
	&:active:hover {
		text-decoration: underline;
		color: ${value('color.text.default')};
	}
`;

TopBannerLink.displayName = 'TopBannerLink';
